<template>
    <div class="admin">
        <div class="admin-title">
            <p class="sub-title">網站管理者</p>
            <p class="title">Admin System</p>
        </div>
        <div class="admin-link">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>管理者系統</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="admin-content">
            <div class="admin-menu">
                <AdminMenu />
            </div>
            <component :is="adminInfo" />
        </div>
        

    </div>
</template>

<script>
import AdminMenu from './meun'
import activity from './activity'
import activityList from './activityList'
import activityInfo from './activityInfo'
import leads from './leads'
import memberList from './memberList'
import amidInfo from './amidInfo'
export default {
    name: 'admin',
    components: {
        AdminMenu,
        activity,
        activityList,
        activityInfo,
        leads,
        memberList,
        amidInfo,
        
    },
    data() {
        return {
            checked: false,
            Username: '',
            Password: '',
        }
    },
    computed: {
        adminInfo() {
            return this.$route.name
            console.log( this.$route.name)
            return 'memberList'
        }
        
    }

}
</script>

<style lang='scss' scoped>
.admin {
    margin-top: 105px;
    .admin-title {
        background: url('~@/assets/img/admin/admin-banner.png') no-repeat center;
        background-size: cover;
    }
        
    .admin-content {
        display: flex;
        @media screen and (max-width: 900px){  
            display: block;
        }
    }
}


</style>


