<template>
    <div class="menu">
        <router-link to = '/admin/amidInfo' class="edit"><i class="el-icon-s-tools" /></router-link>
        <div class="menu-account">
            <div class="head">
                <i class="el-icon-user-solid" />
                <i class="el-icon-camera-solid"/>
            </div>
            <p class="name">Aston Martin</p>
            <div class="login-time">
                <i class="el-icon-time" />
                <div>
                    <p class="time">2021/11/03 AM 10:25</p>
                    <p class="sub-ip">IP: 127.0.0.1</p>
                </div>
            </div>
            
        </div>
        <div class="menu-btn">
            <router-link to = '/admin/leads' :class="{ 'active' : leads.includes(openMenu)}" >
                <img src="~@/assets/img/admin/light.png" alt="" srcset="">
                <p class="num">98 <span>/ 1248</span></p>
                Leads資訊
            </router-link>
            <span class="line"/>
            <router-link to = '/admin/memberList' :class="{ 'active' : memberList.includes(openMenu)}" >
                <img src="~@/assets/img/admin/man.png" alt="" srcset="">
                <p class="num">1,100</p>
                會員資訊
            </router-link>
            <span class="line"/>
            <router-link to = '/admin/activity' :class="{ 'active' : activity.includes(openMenu)}">
                <img src="~@/assets/img/admin/month.png" alt="" srcset="">
                <p class="num">25</p>
                活動資訊
            </router-link>
        </div>
        <div class="menu-info"> 
            <router-link :to ='`/admin/${item.path}`'  v-for="item in menu[openMenu]" :key="item.path" :class="{'active' : item.path === page}">
                <p>{{item.name}}</p>
                <i class="el-icon-arrow-left" />
            </router-link>
            
        </div>

    </div>
</template>

<script>
export default {
    name:'adminMenu',
    data() {
        return {
            menu: {
                leads : [
                ],
                memberList : [
                ],
                activity: [
                    {
                        name:'建立活動',
                        path: 'activity',
                    },
                    {
                        name:'活動一覽',
                        path: 'activityList',
                    },
                ]
            },
            leads:['leads'],
            memberList: ['memberList'],
            activity: ['activity','activityList','activityInfo'],
        }
    },
    computed: {
        openMenu() {
            if(this.leads.includes(this.$route.name)){
                return 'leads'
            }else if(this.memberList.includes(this.$route.name)){
                return 'memberList'
            }else if(this.activity.includes(this.$route.name)) {
                return 'activity'
            }
        },
        page() {
            return this.$route.name
        },
    },
    methods: {  
        
    }
}
</script>

<style lang="scss" scoped>
.menu {
    background: #333333;
    width: 475px;
    height: 100%;
    position: relative;
    border-right: 2px solid #ef6d38;
    padding: 20px 0;
    box-sizing: border-box;
    .edit {
        position: absolute;
        right: 20px;
        top: 20px;
        font-size: 30px;
        color: #ef6d38;
    }
    .menu-account {
        padding: 50px 0;
        text-align: center;
        color: #dddddd;
        .head {
            background: #fff;
            border: 2px solid #ef6d38;
            width: 150px;
            height: 150px;
            border-radius: 75px;
            text-align: center;
            position: relative;
            margin: auto;
            
            .el-icon-user-solid {
                color: #ef6d38;
                font-size: 120px;
                line-height: 150px;
            }
            .el-icon-camera-solid {
                background: #ef6d38;
                color: #fff;
                position: absolute;
                bottom: 10px;
                right: -5px;
                font-size: 30px;
                padding: 5px;
                border-radius: 20px;
                border: 2px solid #fff;
            }
        }
        .name {
            margin: 35px 0;
            font-size: 30px;
            font-weight: bold;
        }
        .login-time {
            display: flex;
            justify-content: center;
            text-align: left;
            align-items: center;
            i {
                background: #ef6d38;
                border-radius: 5px;
                padding: 10px;
                font-size: 25px;
                margin-right: 10px;
            }
            .time {
                color: #ef6d38;
                font-size: 18px;
                font-weight: bold;
            }

        }
        
    }
    .menu-btn {
        display: flex;
        border-right: 8px solid #ef6d38;
        a {
            width: 33%;
            text-align: center;
            padding: 20px 0;
            color: #dddddd;
        }
        .num {
                color: #ef6d38;
                margin: 10px 0;
                font-size: 30px;
                font-weight: bold;
                span {
                    color: #dddddd;
                    font-size: 16px;
                }
            }
        .line {
            width: 2px;
            display: block;
            background: linear-gradient(to bottom, transparent 0%,#ccc 50%,transparent 100%);
        }
        .active {
            background: linear-gradient(to bottom, transparent 0%,rgb(255, 255, 255,0.2) 50%,transparent 100%);
            position: relative;
            &::after {
                content: "〉";
                position: absolute;
                bottom: -35px;
                right: calc(50% - 20px);
                transform: rotate(90deg);
                color: #ef6d38;
                font-size: 30px;
                font-weight: bold;

            }
        }
    }
    .menu-info {
        margin: 40px 0;
        a {
            width: 80%;
            background:linear-gradient(125deg, #fff 80%, #555555 0) bottom right;
            display: block;
            margin: 20px auto;
            padding: 15px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            box-shadow: 0px 3px 7px -4px #4C4C4C;
            
            p {
                padding-left: 20px;
                border-left: #ef6d38 5px solid;
            }
            i {
                color: #fff;
            }
        }
        .active {
            background:linear-gradient(125deg, #fff 80%, #ef6d38 0) bottom right;
        }
    }
    @media screen and (max-width: 900px){
        width: 100%;
        border: none;
        .menu-btn { 
            border: none;
        }
    }
}


</style>


